import { fetchWeatherApi } from 'openmeteo';
const params = {
    "latitude": 51.5085,
    "longitude": -0.1257,
    "current": ["temperature_2m", "relative_humidity_2m", "apparent_temperature", "is_day", "precipitation", "rain", "snowfall"],
    "minutely_15": ["temperature_2m", "relative_humidity_2m", "apparent_temperature"],
    "hourly": ["temperature_2m", "apparent_temperature", "is_day"],
    "daily": ["weather_code", "temperature_2m_max", "temperature_2m_min", "sunrise", "sunset"],
    "timezone": "Europe/London",
    "past_hours": 24,
    "past_minutely_15": 24,
    "forecast_hours": 24,
    "forecast_minutely_15": 96
};
const url = "https://api.open-meteo.com/v1/forecast";
const responses = await fetchWeatherApi(url, params);
// Helper function to form time ranges
const range = (start, stop, step) => Array.from({ length: (stop - start) / step }, (_, i) => start + i * step);
// Process first location. Add a for-loop for multiple locations or weather models
const response = responses[0];
// Attributes for timezone and location
const utcOffsetSeconds = response.utcOffsetSeconds();
const timezone = response.timezone();
const timezoneAbbreviation = response.timezoneAbbreviation();
const latitude = response.latitude();
const longitude = response.longitude();
const current = response.current();
const minutely15 = response.minutely15();
const hourly = response.hourly();
// Note: The order of weather variables in the URL query and the indices below need to match!
const weatherData = {
    current: {
        time: new Date((Number(current.time()) + utcOffsetSeconds) * 1000),
        temperature2m: current.variables(0).value(),
        relativeHumidity2m: current.variables(1).value(),
        apparentTemperature: current.variables(2).value(),
        isDay: current.variables(3).value(),
        precipitation: current.variables(4).value(),
        rain: current.variables(5).value(),
        snowfall: current.variables(6).value(),
    },
    minutely15: {
        time: range(Number(minutely15.time()), Number(minutely15.timeEnd()), minutely15.interval()).map((t) => new Date((t + utcOffsetSeconds) * 1000)),
        temperature2m: minutely15.variables(0).valuesArray(),
        relativeHumidity2m: minutely15.variables(1).valuesArray(),
        apparentTemperature: minutely15.variables(2).valuesArray(),
    },
    hourly: {
        time: range(Number(hourly.time()), Number(hourly.timeEnd()), hourly.interval()).map((t) => new Date((t + utcOffsetSeconds) * 1000)),
        temperature2m: hourly.variables(0).valuesArray(),
        apparentTemperature: hourly.variables(1).valuesArray(),
        isDay: hourly.variables(2).valuesArray(),
    }
};
